.logo {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: url(./images/background@3x.png) no-repeat center / 80%;
    height: 200px;
    width: 200px;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.logo.active {
    height: 100px;
    width: 100px;
}

.logo .img-logo {
    width: 60%;
    height: 45%;
}


