.home {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    background-color: #4F6D7A;
    height: 100%;
}

.home .reverse {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: #FFF;
}

.home .reverse img {
    width: 25px;
    height: 20px;
    margin-right: 10px;
}

.home .gear {
    position: absolute;
    top: 20px;
    right: 20px;
}

.home .gear img {
    height: 25px;
    width: 25px;
}


